.main {
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: #D6EDFC;
    overflow: hidden;
    max-width: 100%;
    height: 88vh;
    justify-content: center;
}

.login-form {
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding: 15px;
}

/*checkbox to switch from sign up to login*/
#chk {
    display: none;
}

/*Login*/
.login {
    position: relative;
    width: 100%;
    height: 100%;
}

.login label {
    margin: 1% 0 3%;
}

label {
    color: #000000;
    font-size: 2rem;
    justify-content: center;
    display: flex;
    font-weight: bold;
    cursor: pointer;
    transition: .5s ease-in-out;
}

.login-input {
    width: 95%;
    height: 40px;
    background: #ffffff;
    padding: 10px;
    border: none;
    outline: none;
    border-radius: 4px;
}

/*Register*/
.register {
    background: #eee;
    border-radius: 60% / 10%;
    transform: translateY(5%);
    transition: .8s ease-in-out;
}

.register label {
    color: #303030;
    transform: scale(.6);
}
