/*Button*/
.login-form .button {
    width: 100%;
    height: 40px;
    margin: 12px auto 7%;
    color: #fff;
    vertical-align: center;
    background: hsl(204, 100%, 52%);
    font-size: 1rem;
    font-weight: bold;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: .2s ease-in;
}

.form .button:hover {
    background-color: #016db6;
}

.accountImg {
    display: inline-block;
    width: 30%;
    aspect-ratio: 1/1;
    border-radius: 50%;
    margin: 10px auto 0;
}
  