#outLine {
    height: 100vh;
    background-color: rgb(207, 200, 250);
}

#topBox {
    background-color: #c5e5fa;
    border-radius: 0 0 50px 50px;
    height: 30%;
    width: 100%;
}

#topText {
    margin-top: -10px;
}

#middleBox {
    background-color: #ffffff;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 30px;
    height: 16%;
    width: 90%;
    margin: -5% auto 0 auto;
}

#middleBoxImg {
    background-color: #309cff;
    height: 40px;
    width: 40px;
    margin-left: 22px;
    margin-bottom: -70px;
}

#middleBoxs {
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 10px;
    background-color: #c1f8ff;
    font-weight: bold;
    font-size: 20px;
    height: 4.5rem;
    width: 4.5rem;
    margin: 1rem;
    line-height: 30px;
}

#middleLine {
    margin: 6.5% auto 4.4% auto;
    width: 95%;
    height: 2px;
    background-color: #000000;
}

#bottomBox {
    display: block;
    width: 93.7%;
    height: 45%;
    margin: 0 auto 20px auto;
    border: solid 2px #2babff;
    border-radius: 20px;
}

#today_Task {
    font-weight: bold;
    font-size: 20px;
    margin-top: 2%;
    margin-left: 5%;
}

.detailBox {
    position: fixed;
    z-index: 12;
    background-color: yellow;
    height: 215px;
    border-radius: 10px;
    margin-top: -8px;
    margin-bottom: -200px;
    width: 310px;
}

#addTaskBtn {
    align-content: center;
    border-radius: 30px;
    background-color: #ffffff;
    margin-top: 3px;
}

#taskList {
    margin: 0 auto;
    width: 90%;
    height: 67%;
    overflow-y: auto;
}

#detailBtn {
    align-content: center;
    border: solid black 0.5px;
    border-radius: 30px;
    background-color: #ffffff;
}

#task {
    background-color: yellow;
    border-radius: 20px;
    height: 53px;
    width: 100%;

}
