

#leftSpace{
    flex-grow:0.7;
}

#rightSpace{
    flex-grow:1;
}

#title{
    color:black;
    text-align: center;
    flex-grow:1;
}

.button-container {
    display: flex;
    background-color: #afdffe;
    /*width: auto;*/

    position: fixed;
    bottom: 0;
    height: 4rem;
    align-items: center;
    justify-content: space-around;
    box-shadow: rgba(0, 0, 0, 0.35) 0 5px 15px,
    rgba(127, 198, 255, 0.5) 5px 10px 15px;

}

.underBarBtn {
    outline: 0;
    border: 0;
    padding: 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    transition: all ease-in-out 0.3s;
    cursor: pointer;

}

.underBarBtn:hover {
    transform: translateY(-3px);
}

.btnIcon {
    font-size: 1.8rem;
}
.circle{
    width: 100px;
    height: 100px;
    background-color: white;
    border-radius: 50%;
}